import React from 'react';
import axios from 'axios';
import {
  Form,
  Select,
  Input,
  InputNumber,
  Button,
  Upload,
  DatePicker,
} from 'antd';
import { UploadOutlined } from '@ant-design/icons';

import { addAlternator as cms } from '../../cms';
import { codeRule, nameRule, ampRule, connectionRule, fanPulleyRule, fixingPointRule } from './rules';
import { config, alternatorApi } from '../../config';
import { UserContext } from '../../contexts';
import { UseContext } from '../../hoc';

const { Option } = Select;
const formItemLayout = {
  labelCol: {
    span: 6,
  },
  wrapperCol: {
    span: 14,
  },
};

const getImageUrl = fileList => fileList.map(val => (val.response ? val.response.secure_url : undefined)).filter(val => val);

const normFile = e => {
  console.log('Upload event:', e);

  if (Array.isArray(e)) {
    return e
  }
  return e && e.fileList;
};

class AddAlternator extends React.Component {
  onFinish = async values => {
    try {
      const { getAuthHeader, history } = this.props;
      values.imgUrl = values.imgUrl ? getImageUrl(values.imgUrl) : [];
      if (values.startYear) {
        values.startYear = values.startYear.year();
      }
      if (values.endYear) {
        values.endYear = values.endYear.year()
      }
      console.log('Received values of form: ', values);
      const response = await axios.post(`${alternatorApi.add}`, values, getAuthHeader());
      history.push('/alternators');
      console.log("onFinish response", response)
    } catch (err) {
      console.log("onFinish Error:", err);
    }
  };

  render() {
    return (<Form
      name="validate_other"
      {...formItemLayout}
      onFinish={this.onFinish}
      initialValues={{
        'input-number': 3,
        'amp': 10,
        'checkbox-group': ['A', 'B'],
        'fixingPoint': 3,
        rate: 3.5,
      }}
    >
      <Form.Item label={cms.codeLable} name="code" rules={codeRule}>
        <Input maxLength={20} />
      </Form.Item>

      <Form.Item label={cms.nameLable} name="name" rules={nameRule}>
        <Input maxLength={50} />
      </Form.Item>

      <Form.Item label={cms.ampLabel} rules={ampRule}>
        <Form.Item name="amp" noStyle>
          <InputNumber min={1} max={250} />
        </Form.Item>
        <span className="ant-form-text"> A</span>
      </Form.Item>

      <Form.Item label={cms.connectionsLabel} name="connections" rules={connectionRule}>
        <Input maxLength={50} />
      </Form.Item>

      <Form.Item label={cms.fanTypeLabel} name="fanPulleyType" rules={fanPulleyRule}>
        <Input maxLength={50} />
      </Form.Item>

      <Form.Item label={cms.fixingPointLabel} name="fixingPoint" rules={fixingPointRule}>
        <InputNumber min={1} max={10} />
      </Form.Item>

      <Form.Item
        name="fuelType"
        label={cms.fuelTypeLabel}
      >
        <Select mode='multiple'>
          <Option value="Petrol">Petrol</Option>
          <Option value="Diesel">Diesel</Option>
        </Select>
      </Form.Item>

      <Form.Item
        name="transmissionType"
        label={cms.transmissionLabel}
      >
        <Select mode='multiple'>
          <Option value="Manual">Manual</Option>
          <Option value="Automatic">Automatic</Option>
        </Select>
      </Form.Item>

      <Form.Item label={cms.makeLabel} name="make">
        <Input maxLength={50} />
      </Form.Item>

      <Form.Item label={cms.modelLabel} name="model">
        <Input maxLength={50} />
      </Form.Item>

      <Form.Item name="startYear" label={cms.startLabel} >
        <DatePicker picker="year" />
      </Form.Item>

      <Form.Item name="endYear" label={cms.endLabel} >
        <DatePicker picker="year" format="YYYY" />
      </Form.Item>

      <Form.Item label={cms.engineCodeLabel}>
        <Form.Item name="engineCodes" noStyle>
          <Select mode="tags" style={{ width: '100%' }} tokenSeparators={[',']} />
        </Form.Item>
      </Form.Item>

      <Form.Item
        name="imgUrl"
        label={cms.productImageLabel}
        valuePropName="fileList"
        getValueFromEvent={normFile}
      >
        <Upload name="file"
          multiple={true}
          action={`${config.cloudinaryUrl}/upload`}
          data={{ upload_preset: config.cloudinaryuploadPreset, folder: 'grover' }}
          method="POST"
          listType="picture"
        >
          <Button>
            <UploadOutlined /> Click to upload
          </Button>
        </Upload>
      </Form.Item>

      <Form.Item
        wrapperCol={{
          span: 12,
          offset: 6,
        }}
      >
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
    </Form>
    )
  }
}

export default UseContext(UserContext)(AddAlternator);